*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.display{
    margin-bottom: 30px !important;
    width: 100%;
    position: relative;
    &__indicator{
        position: absolute;
        bottom: 10px;
        right: 10px;
        color: #1976d2;
        font-weight: 900;
    }
}

.container{
    padding: 20px 0px;
    display: flex !important;
    justify-content: center;
    flex-direction: column;
    min-height: 100vh;
}

.submitButton{
    position: fixed !important;
    top: 20px;
    right: 20px;
    z-index: 50;
}

.preloader {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}